.home{
    height: 100vh;
    position: relative;
    background-color: $color-gray-light;
}

.search-section{
    position: relative;
    top: 40%;
    left:50%;
    transform: translate(-50%, -50%);
    text-align: center; 
   
     &__travico{
         font-size: 6rem;
         margin-bottom: 6rem;
         color: $color-primary;
         @include respond(tab-land){
             font-size: 4rem;
         } 
     }
     &__searchBox{
        background-color: $color-white;
        width: 70%;
        position: relative;
        top: 30%;
        left:50%;
        transform: translate(-50%, -50%);
        text-align: center; 
        border-radius: 50rem;
        padding: 1rem;
        display: flex;
        align-items: center;
        justify-content: center;
        box-shadow: 0 1rem 2rem rgba($color:#998e8e, $alpha: .5); 
        transition: all .2s;
        &:hover {
            box-shadow: 0 1rem 2rem rgba($color:#998e8e, $alpha: 1); 
            width: 70.5%;
          }  
        @include respond(tab-land){
            width: 90%;
            &:hover {
                box-shadow: 0 1rem 2rem rgba($color:#998e8e, $alpha: 1); 
                width: 90.2%;
              }
        } 
        @include respond(phone){
            
            width: 90%;
            &:hover {
                box-shadow: 0 1rem 2rem rgba($color:#998e8e, $alpha: 1); 
                width: 90.5%;
              }
            
        }

    }
    &__icon{
        font-size: 2rem;
        color: $color-gray-dark;
        margin-left: .7rem;
        @include respond(tab-land){
           font-size: 1.2rem;
           margin-left: .5rem;
        }
       
    }
    &__input{
        flex: 1;
        height: 4rem;
        border: none;
        outline: none;
        font-size: 2rem ;
        padding-left: 1rem;
        margin-right: 1rem;
        margin-top: -1rem;
        margin-bottom: -1rem;
        background-color: rgba($color: $color-white, $alpha: 1.0);
        color: $color-black;
        &::-webkit-input-placeholder{
            font-weight: 100;
            color: rgb(192, 179, 179);
            font-size: 1.5rem;
        }
        @include respond(phone){
            
          
            font-size: 1.7rem ;
            
        }
    }
    &__btn{
        border: none;
        border-radius: 50px;
        padding: 1rem 2rem;
        margin-top: 1rem;
        box-shadow: 0 1rem 2rem rgba($color:#998e8e, $alpha: .5); 
        background-color: $color-white;
        font-size: 2rem;
        color: $color-primary;
        text-transform: capitalize;
        transition: all .2s;
        &:active{
            transform: translateY(2px);
        }
        &:hover{
            box-shadow: 0 1rem 2rem rgba($color:#998e8e, $alpha: 1); 
            transform: scale(1.02);
        }
    
        @include respond(tab-land){
            font-size: 1.5rem;
            padding: .8rem 1.8rem;
            
         }
    }

}
